// Populate the region select elements when the page loads

function getSizesForRegion(region) {
  const sizes = {
    US: [
      4.5, 5, 5.5, 6, 6.5, 7, 7.5, 8, 8.5, 9, 9.5, 10, 10.5, 11, 11.5, 12, 12.5,
      13, 13.5, 14, 15,
    ],
    EU: [
      36.5, 37.5, 38, 38.5, 39, 40, 40.5, 41, 42, 42.5, 43, 44, 44.5, 45, 45.5,
      46, 46.5, 47, 47.5, 48, 48.5, 49.5,
    ],
    UK: [
      4, 4.5, 5, 5.5, 6, 6.5, 7, 7.5, 8, 8.5, 9, 9.5, 10, 10.5, 11, 11.5, 12,
      12.5, 13, 14,
    ],
    KR: [
      225, 230, 235, 240, 245, 250, 255, 260, 265, 270, 275, 280, 285, 290, 295,
      300, 305, 310, 315, 320,
    ],
    CN: [
      230, 235, 240, 245, 250, 255, 260, 265, 270, 275, 280, 285, 290, 295, 300,
      305, 310, 315, 320, 325, 330,
    ],
  };

  return sizes[region];
}

function setDefaultSize() {
  let defaultSize = 7; // Set the default size to US size 7

  // Set the default size button as active
  let sizeButtons = document.querySelectorAll('.size-buttons-container button');

  sizeButtons.forEach((button) => {
    if (parseFloat(button.value) === defaultSize) {
      button.classList.add('active');
      selectedSize = defaultSize;
    } else {
      button.classList.remove('active');
    }
  });
}

document.addEventListener('DOMContentLoaded', setDefaultSize);

window.onload = function () {
  let regions = ['US', 'EU', 'UK', 'KR', 'CN']; // Add your regions here

  regions.forEach((region) => {
    let option = new Option(region, region);
    document.getElementById('source_region').add(option);
    document.getElementById('target_region').add(option.cloneNode(true)); // Clone the option for the target_region select element
  });

  populateSizes(regions[0], 'size-button'); // Default buttons for the first region
};
